@use '_mixin' as m;

.account-reviews-item {
  padding: m.toRem(16) m.toRem(30);
  border: 1px solid var(--grey-border);
  border-radius: 12px;
  @media (max-width: m.toEm(768)) {
    padding: m.toRem(16) m.toRem(15);
  }

  // .account-reviews-item__header

  &__header {
    margin-bottom: m.toRem(16);
    display: flex;
    align-items: center;
    gap: m.toRem(15);
    @media (max-width: m.toEm(768)) {
      flex-wrap: wrap;
    }
  }

  // .account-reviews-item__avatar

  &__avatar {
    flex-shrink: 0;
    width: m.toRem(40);
    height: m.toRem(40);
    border-radius: 50%;
    overflow: hidden;
    object-position: center;
    object-fit: cover;
  }
  // .account-reviews-item__logo

  &__logo {
    flex-shrink: 0;
    width: m.toRem(60);
    height: m.toRem(60);
    object-position: center;
    object-fit: cover;
  }

  // .account-reviews-item__info

  &__info {
    flex-grow: 1;
  }

  // .account-reviews-item__name

  &__name {
    font-size: m.toRem(14);
    line-height: 157%;
    color: var(--text);
  }

  // .account-reviews-item__date

  &__date {
    font-size: m.toRem(12);
    color: #b2b2b2;
  }

  
  &__address {
    font-size: m.toRem(14);
    color: var(--text);
  }

  &__company {
    font-weight: 700;
    font-size: m.toRem(20);
    line-height: 120%;
  }

  // .account-reviews-item__rating

  &__rating {
    align-self: start;
  }

  // .account-reviews-item__body

  &__body {
    margin-bottom: m.toRem(16);
  }

  // .account-reviews-item__title

  &__title {
    margin-bottom: m.toRem(6);
    font-weight: 700;
    line-height: 137%;
    @media (max-width: m.toEm(768)) {
      font-size: m.toRem(15);
    }
  }

  // .account-reviews-item__text

  &__text {
    font-size: m.toRem(14);
    font-size: 14px;
    line-height: 157%;
    @media (max-width: m.toEm(768)) {
      font-size: m.toRem(13);
    }
  }

  // .account-reviews-item__answer

  &__answer {
    margin-bottom: m.toRem(16);
  }

  // .account-reviews-item__answer-body

  &__answer-body {
  }

  // .account-reviews-item__answer-button

  &__answer-button {
    padding: m.toRem(7) m.toRem(16);
    display: flex;
    gap: m.toRem(7);
    align-items: center;
    color: var(--green);
    border: 1px solid var(--green);
    background: #d8fff5;
    border-radius: 500px;
    transition: background 0.3s ease 0s;
    @media (any-hover: hover) {
      &:hover {
        background: #fff;
      }
    }

    &._active {
      .account-reviews-item__answer-button-icon {
        transform: scaleY(-1);
      }
    }
  }

  // .account-reviews-item__answer-button-icon

  &__answer-button-icon {
    width: m.toRem(8);
    height: m.toRem(14);
    transition: transform 0.3s ease 0s;
  }

  // .account-reviews-item__answer-text

  &__answer-wrapper {
    padding-top: m.toRem(16);
  }

  &__answer-text {
    padding: m.toRem(24) m.toRem(24);
    background: #f7f9ff;
    border-radius: 12px;
    font-size: m.toRem(14);
    line-height: 157%;
    @media (max-width: m.toEm(768)) {
      padding: m.toRem(15) m.toRem(15);
      font-size: m.toRem(13);
    }
  }

  // .account-reviews-item__actions

  &__actions {
    display: flex;
    align-items: center;
    justify-content: end;
    gap: m.toRem(20);
  }

  // .account-reviews-item__action

  &__action {
    display: flex;
    align-items: center;
    gap: m.toRem(4);
    fill: none;
    font-size: m.toRem(14);

    &--edit {
      color: var(--green);
      .account-reviews-item__action-icon {
        stroke: var(--green);
      }
    }

    &--del {
      color: #e33629;
      .account-reviews-item__action-icon {
        stroke: #e33629;
      }
    }
  }

  // .account-reviews-item__action-icon

  &__action-icon {
    width: m.toRem(20);
    height: m.toRem(20);
  }
}
