@use '_mixin' as m;

.reviews-company {
  --red: #e10e0e;
  --light-red: #f77;
  --orange: #fbbf24;
  --teal: #72ddc3;
  --green: #13c296;

  // .reviews-company__container

  &__container {
  }

  // .reviews-company__info

  &__info {
    display: flex;
    gap: m.toRem(25);
    @media (max-width: m.toEm(992)) {
      flex-direction: column;
    }
  }

  // .reviews-company__card

  &__card {
  }

  // .reviews-company__rating

  &__rating {
    flex: 0 0 m.toRem(370);
    @media (max-width: m.toEm(640)){
        flex: 0 1 auto;
    }
  }

  // .reviews-company__body

  &__body {
    display: flex;
    position: relative;
    gap: m.toRem(30);
    @media (max-width: m.toEm(992)) {
      flex-direction: column-reverse;
    }
  }

  // .reviews-company__all-reviews

  &__all-reviews {
    flex: 1 1 auto;
     scroll-margin-top: m.toRem(20)
  }

  // .reviews-company__filter

  &__filters {
    margin-bottom: m.toRem(25);
    display: flex;
    flex-wrap: wrap;
    gap: m.toRem(15);
    justify-content: space-between;
  }

  &__filters-title {
    font-weight: 600;
    font-size: m.toRem(20);
    line-height: 150%;
    @media (max-width: m.toEm(992)) {
      font-size: m.toRem(18);
    }
  }

  &__filters-items {
    display: flex;
    gap: m.toRem(10);
  }

  &__filters-input {
    height: m.toRem(40);
    max-width: m.toRem(180);
  }

  // .reviews-company__items

  &__items {
    margin-bottom: m.toRem(50);
    display: flex;
    flex-direction: column;
    gap: m.toRem(30);
    max-width: m.toRem(770);
  }

  // .reviews-company__actions

  &__actions {
  }

  &__sticky {
    min-width: m.toRem(370);
    position: sticky;
    top: m.toRem(20);
    display: flex;
    flex-direction: column;
    gap: m.toRem(20);
    @media (max-width: m.toEm(640)){
        min-width: auto;
    }
  }

  // .reviews-company__write

  &__write {
    width: 100%;
    min-height: m.toRem(48);
  }
}
