@use '_mixin' as m;

body {
  background: var(--white);

  &._lock {
    overflow: hidden;
    touch-action: none;
    overscroll-behavior: none;
  }
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;

  @supports (overflow: clip) {
    overflow: clip;
  }

  // Прижимаем footer
  > main {
    flex: 1 1 auto;
  }

  // фикс для слайдеров
  > * {
    min-width: 0;
  }
}

[class*='__container'] {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  width: 100%;
}

.page-top {
  background: var(--bg-gradient);
}

.page {
  padding-top: m.toRem(40);

  @media (max-width: m.toEm(992)) {
    padding-top: m.toRem(15);
  }
}

.account {

  &__container {
    display: flex;
    gap: m.toRem(30);
    align-items: stretch;
    @media (max-width: m.toEm(1280)) {
      gap: m.toRem(20);
    }
    @media (max-width: m.toEm(992)) {
      flex-direction: column;
    }
  }

  &__nav {
    flex-shrink: 0;
    max-width: m.toRem(270);
    width: 100%;
    @media (max-width: m.toEm(992)) {
      max-width: 100%;
      position: sticky;
      z-index: 1;
      top: 0;
    }
  }
}
