@use '_mixin' as m;

.filter-company-reviews {
  padding: m.toRem(19) m.toRem(20) m.toRem(19) m.toRem(30);
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: m.toRem(8);
  border: 1px solid var(--grey-border);
  border-radius: 8px;

  @media (max-width: m.toEm(1280)) {
    padding: m.toRem(19) m.toRem(15);
  }

  // .filter-company-reviews__item

  &__item {
  }

  // .filter-company-reviews__input

  &__input {
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &:checked + .filter-company-reviews__label::after {
      transform: scale(1);
    }

    &:checked + .filter-company-reviews__label::before {
      background-color: var(--green);
      outline: 1.25px solid var(--green);
    }

    &#star5:checked + .filter-company-reviews__label {
      .filter-company-reviews__percent {
        background-color: var(--green);
      }
      .filter-company-reviews__star {
        color: var(--green);
      }
    }
    &#star4:checked + .filter-company-reviews__label {
      .filter-company-reviews__percent {
        background-color: var(--teal);
      }
      .filter-company-reviews__star {
        color: var(--teal);
      }
    }
    &#star3:checked + .filter-company-reviews__label {
      .filter-company-reviews__percent {
        background-color: var(--orange);
      }
      .filter-company-reviews__star {
        color: var(--orange);
      }
    }
    &#star2:checked + .filter-company-reviews__label {
      .filter-company-reviews__percent {
        background-color: var(--light-red);
      }
      .filter-company-reviews__star {
        color: var(--light-red);
      }
    }
    &#star1:checked + .filter-company-reviews__label {
      .filter-company-reviews__percent {
        background-color: var(--red);
      }
      .filter-company-reviews__star {
        color: var(--red);
      }
    }
  }

  // .filter-company-reviews__label

  &__label {
    width: 100%;
    display: flex;
    align-items: center;
    justify-items: start;
    position: relative;
    cursor: pointer;

    &::before {
      content: '';
      align-self: flex-start;
      flex-shrink: 0;
      height: m.toRem(19);
      width: m.toRem(19);
      border-radius: 4px;
      margin: 0 m.toRem(12) 0 0;
      outline: 1.25px solid var(--grey-border);
      transition: background 0.3s ease, outline 0.3s ease;
    }

    &::after {
      content: '';
      width: m.toRem(11);
      height: m.toRem(8);
      position: absolute;
      top: m.toRem(6);
      left: m.toRem(5);
      background: url('../img/svg/check.svg') center / contain no-repeat;
      transform: scale(0);
      transition: transform 0.3s ease;
    }
  }

  &__info {
    display: flex;
    width: 100%;
    align-items: center;
    gap: m.toRem(15);
    font-size: m.toRem(15);
  }

  // .filter-company-reviews__star

  &__star {
    transition: color 0.3s ease;
  }

  // .filter-company-reviews__line

  &__line {
    padding: m.toRem(4) 0;
    flex: 1 0 auto;
    height: m.toRem(7);
    background: #ededed;
    border-radius: 62px;
    overflow: hidden;
    position: relative;
  }
  //.filter-company-reviews__percent
  &__percent {
    position: absolute;
    top: 0;
    left: 0;
    display: block;

    height: 100%;
    background-color: var(--main-color);
    border-radius: 62px;
    transition: background 0.3s ease 0s;
  }

  // .filter-company-reviews__value

  &__value {
    flex: 0 0 m.toRem(40);
    // text-align: end;
  }
}
