@use '_mixin' as m;

.account-nav {
  padding: m.toRem(10) m.toRem(1);
  width: 100%;
  position: sticky;
  top: 20px;
  background-color: var(--white);
  border: 1px solid var(--grey-border);
  border-radius: 6px;

  @media (max-width: m.toEm(992)) {
    padding: 0;
  }

  // .account-nav__list

  &__list {
    display: flex;
    flex-direction: column;
    gap: m.toRem(10);
    @media (max-width: m.toEm(992)) {
      flex-direction: row;
      gap: m.toRem(4);
    }
  }

  // .account-nav__item

  &__item {
    flex: 1 1 auto;
  }

  // .account-nav__link

  &__link {
    width: 100%;
    padding: m.toRem(8) m.toRem(20);
    color: var(--text);
    line-height: 150%;

    transition: background 0.3s ease, color 0.3s ease;

    @media (any-hover: hover) {
      &:hover {
        background-color: var(--green_05);
        color: var(--green);
      }
    }
    &.active {
      background-color: var(--green_05);
      color: var(--green);
      pointer-events: none;
      font-weight: 600;
    }

    @media (max-width: m.toEm(992)) {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    @media (max-width: m.toEm(768)) {
      padding: m.toRem(8) m.toRem(5);
      font-size: m.toRem(15);
      text-align: center;
    }

    .d-desktop {
      display: inline;
    }
    .d-mobile {
      display: none;
    }

    @media (max-width: 768px) {
      .d-desktop {
        display: none;
      }
      .d-mobile {
        display: inline;
      }
    }
  }
}
