@use '_mixin' as m;

.company-card {
  padding-bottom: m.toRem(30);
  margin-bottom: m.toRem(30);
  display: grid;
  grid-template-columns: auto m.toRem(370);
  gap: m.toRem(34) m.toRem(15);
  border-bottom: 1px solid var(--grey-border);

  @media (max-width: m.toEm(992)) {
    grid-template-columns: auto;
  }

  &__gauge {
    @media (min-width: m.toEm(993)) {
      grid-row: span 2;
    }
  }

  // .company-card__header

  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: m.toRem(20);
  }

  // .company-card__logo

  &__logo {
    flex-shrink: 0;
    width: m.toRem(149);
    height: m.toRem(149);
    object-fit: contain;
    object-position: center;
    @media (max-width: m.toEm(1280)) {
      width: m.toRem(120);
      height: m.toRem(120);
    }
    @media (max-width: m.toEm(768)) {
      width: m.toRem(100);
      height: m.toRem(100);
    }
  }

  // .company-card__info

  &__info {
    display: flex;
    flex-direction: column;
    align-items: start;
  }

  // .company-card__name

  &__name {
    margin-bottom: m.toRem(10);
    font-family: var(--second-family);
    font-weight: 600;
    font-size: m.toRem(32);
    @media (max-width: m.toEm(1280)) {
      font-size: m.toRem(30);
    }
  }

  // .company-card__meta

  &__meta {
    margin-bottom: m.toRem(20);
    display: flex;
    align-items: center;
    gap: m.toRem(8);
  }

  // .company-card__reviews

  &__reviews {
    display: flex;
    align-items: center;
    gap: m.toRem(12);
    font-size: m.toRem(14);
    text-decoration: underline;
    text-decoration-skip-ink: none;
    position: relative;
    @media (any-hover: hover) {
      &:hover {
        text-decoration: none;
      }
    }
    &::after {
      content: '';
      width: m.toRem(5);
      height: m.toRem(5);
      border-radius: 50%;
      background-color: var(--main-color);
    }
  }

  // .company-card__rating

  &__rating {
  }

  // .company-card__actions

  &__actions {
  }

  // .company-card__btn

  &__btn {
    min-height: m.toRem(32);
    min-width: m.toRem(110);
  }

  // .company-card__details

  &__details {
    &:not(:last-child) {
      margin-bottom: m.toRem(16);
    }
  }

  // .company-card__title

  &__title {
    margin-bottom: m.toRem(8);
    font-weight: 600;
    font-size: m.toRem(20);
    line-height: 150%;
  }

  // .company-card__text

  &__text {
    font-size: m.toRem(14);
    color: var(--text);
    line-height: 157%;
  }

  // .company-card__contacts

  &__contacts {
    margin-bottom: m.toRem(8);
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: m.toRem(8);
  }

  // .company-card__contact

  &__contact {
    display: flex;
    gap: m.toRem(10);
    align-items: center;
  }

  &__link {
    display: flex;
    gap: m.toRem(10);
    align-items: center;
    @media (any-hover: hover) {
      &:hover {
        text-decoration: underline;
      }
    }
  }

  // .company-card__icon

  &__icon {
    flex-shrink: 0;
    width: m.toRem(20);
    height: m.toRem(20);
  }

  // .company-card__socials

  &__socials {
    display: flex;
    gap: m.toRem(8);
  }

  // .company-card__social

  &__social {
  }

  // .company-card__social-link

  &__social-link {
  }

  // .company-card__social-icon

  &__social-icon {
    flex-shrink: 0;
    width: m.toRem(18);
    height: m.toRem(18);
    transition: transform 0.3s ease 0s;
    @media (any-hover: hover) {
      &:hover {
        transform: scale(1.1);
      }
    }
  }
}
