@use '_mixin' as m;

.reviews-empty {
  padding-top: m.toRem(30);
  display: flex;
  flex-direction: column;
  align-items: center;

  // .reviews-empty__icon

  &__icon {
    margin-bottom: m.toRem(25);
    width: m.toRem(100);
    height: m.toRem(90);
  }

  // .reviews-empty__title

  &__title {
    margin-bottom: m.toRem(8);
    font-size: m.toRem(24);
    line-height: 125%;
  }

  // .reviews-empty__text

  &__text {
    font-size: m.toRem(14);
    line-height: 157%;
    text-align: center;
    color: var(--text);
  }
}
