@use '_mixin' as m;

.rating {
  display: flex;

  gap: m.toRem(5) m.toRem(10);
  align-items: center;
  justify-content: end;
  flex-wrap: wrap;

  @media (max-width: m.toEm(768)) {
    font-size: m.toRem(14);
    justify-content: start;
  }

  &--green {
    .rating__item-icon {
      stroke: var(--green);
      &.filled {
        fill: var(--green);
      }
    }
  }

  &__items {
    display: flex;
    align-items: center;
    gap: m.toRem(3);
  }

  &__value {
    font-weight: 500;
    line-height: 150%;
  }
  &__value-company {
    font-weight: 700;
    line-height: 150%;
    font-size: m.toRem(14);
  }

  // .rating__item

  &__item {
    font-size: 0;
  }

  // .rating__item-icon

  &__item-icon {
    width: m.toRem(16);
    height: m.toRem(16);
    fill: none;
    stroke: #f59e0b;
    stroke-width: m.toRem(1.5);

    &.filled {
      fill: #f59e0b;
    }
  }
}

.rating-group {
  &__items {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    gap: 3px;

    @media (max-width: 375px) {
      gap: 1px;
    }
  }

  &__item {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;

    cursor: pointer;

    &:checked ~ .rating-group__label {
      background: url('../img/svg/star-green-fill.svg') 0 0 / m.toRem(48)
        no-repeat;
    }

    &:focus + .rating-group__label {
      background: url('../img/svg/star-green-fill.svg') 0 0 / m.toRem(48)
        no-repeat;
    }
  }

  &__label {
    cursor: pointer;
    flex-shrink: 0;
    width: m.toRem(48);
    height: m.toRem(48);
    background: url('../img/svg/star-green.svg') 0 0 / m.toRem(48) no-repeat;
  }
}
