
@use '_mixin' as m;

.switch-btn {
  flex-shrink: 0;
  display: inline-block;
  width: m.toRem(40);
  height: m.toRem(24);
  border-radius: 99px;
  background: rgba(120, 120, 128, 0.4);
  z-index: 0;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
  position: relative;
  transition: background 0.3s ease;
}

.switch-btn::after {
  content: "";
  height: m.toRem(22);
  width: m.toRem(22);
  border-radius: 18px;
  background: #fff;
  top: m.toRem(1);
  left: m.toRem(0);
  transition-duration: 300ms;
  border: 0.5px solid rgba(0, 0, 0, 0.04);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 0px 0px 0 rgba(0, 0, 0, 0.1);
  position: absolute;
  z-index: 1;
  transition: left 0.3s ease 0s;
}

.switch-on {
  background: var(--green);
}

.switch-on::after {
  left: m.toRem(17);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 0px 0px 0 rgba(0, 0, 0, 0.1);
  border: none;
}