@use '_mixin' as m;

.modal {
  --transition-time: 0.3s;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: rgba(17, 25, 40, 0.9);
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  justify-content: center;
  justify-items: center;
  cursor: pointer;
  overflow-y: auto;
  overflow-x: hidden;
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time) ease,
    visibility var(--transition-time) ease;
}

.modal__container {
  margin: m.toRem(30) m.toRem(15);
  padding: m.toRem(35);
  position: relative;
  width: m.toRem(570);
  border-radius: 12px;
  background: var(--white);
  cursor: default;
  display: none;
  border: 1px solid #ff7e7e;

  &--banner {
    width: m.toRem(600);
    padding: m.toRem(30) m.toRem(40);
  }

  @media (max-width: m.toEm(768)) {
    padding: m.toRem(30) m.toRem(15);
    width: auto;
    min-width: 90%;
  }
}

.modal__close {
  position: absolute;
  right: m.toRem(30);
  top: m.toRem(30);

  cursor: pointer;

  &:active,
  &:focus-visible {
    outline: none;
  }


  @media (max-width: m.toEm(576)) {
    right: m.toRem(11);
    top: m.toRem(11);
  }
}

.modal__close-icon {
  width: m.toRem(14);
  height: m.toRem(14);
  transition: fill 0.3s ease;
  fill: var(--white);
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time) ease,
    visibility var(--transition-time) ease;
}

.modal__container.modal-open {
  display: inline-block;
}

.fade {
  transform: scale(0);
  transition: transform 0.8s ease;
}

.fade.animate-open {
  transform: scale(1);
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
  transition: transform var(--transition-time) ease,
    opacity var(--transition-time) ease;
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
  transition: transform var(--transition-time) ease,
    opacity var(--transition-time) ease;
}

.custom.animate-open {
  opacity: 1;
  transform: rotate(180deg);
  transition: transform var(--transition-time), opacity var(--transition-time);
}

.disable-scroll {
  position: relative;
  overflow: hidden;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

