@use '_mixin' as m;

.review {
  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: m.toRem(20);

  @media (max-width: m.toEm(768)) {
    gap: m.toRem(10);
  }

  &__card {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: start;

    &--response {
      padding-left: m.toRem(97);
      padding-top: m.toRem(13);
      position: relative;

      @media (max-width: m.toEm(768)) {
        padding-left: m.toRem(30);
      }

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: m.toRem(61);
        width: m.toRem(20);
        height: m.toRem(37);
        background: url('../img/svg/line.svg') 0 0 / contain no-repeat;

        @media (max-width: m.toEm(768)) {
          left: 0;
        }
      }
    }
  }

  // .review__header

  &__header {
    margin-bottom: m.toRem(7);
    padding-top: m.toRem(7);
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: start;
    gap: m.toRem(16);

    @media (max-width: m.toEm(768)) {
      gap: m.toRem(13) m.toRem(10);
    }
  }

  // .review__avatar

  &__avatar {
    flex-shrink: 0;
    margin-top: m.toRem(-7);
    width: m.toRem(45);
    height: m.toRem(45);
    border-radius: 50%;
    object-position: center;
    object-fit: cover;
    overflow: hidden;

    @media (max-width: m.toEm(768)) {
      margin-top: m.toRem(-3);
      width: m.toRem(38);
      height: m.toRem(38);
    }

    // transform: translate(0px, m.toRem(-7));
  }

  // .review__user

  &__user {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
  }

  // .review__name

  &__name {
    margin-bottom: m.toRem(6);
    font-weight: 700;
    font-size: m.toRem(15);
    @media (max-width: m.toEm(768)){
        font-size: m.toRem(14);
    }
  }

  // .review__info

  &__info {
    display: flex;
    align-items: center;
    font-size: m.toRem(12);
    color: var(--text);
    gap: m.toRem(4);
  }

  // .review__country

  &__country {
    display: flex;
    align-items: center;
    gap: m.toRem(4);
    position: relative;

    &::after {
      content: '';
      width: m.toRem(3);
      height: m.toRem(3);
      border-radius: 50%;
      background-color: var(--text);
    }
  }

  // .review__amount

  &__amount {
  }

  // .review__rating

  &__rating {
    @media (max-width: m.toEm(768)) {
      width: 100%;
      order: 3;
    }
  }

  // .review__time

  &__time {
    margin-left: auto;
    font-size: m.toRem(12);
    color: var(--text);

    @media (max-width: m.toEm(768)) {
      order: 2;
    }
  }

  &__content {
    padding-left: m.toRem(61);

    @media (max-width: m.toEm(768)) {
      padding-left: 0;
    }
  }

  // .review__title

  &__title {
    margin-bottom: m.toRem(7);
    font-weight: 700;
    line-height: 137%;
    @media (max-width: m.toEm(768)){
        font-size: m.toRem(15);
    }
  }

  // .review__text

  &__text {
    &:not(:last-child) {
      margin-bottom: m.toRem(6);
    }

    font-size: m.toRem(14);
    line-height: 157%;
    @media (max-width: m.toEm(768)){
        font-size: m.toRem(13);
         line-height: 147%;
    }
  }

  // .review__date

  &__date {
    font-size: m.toRem(14);
    line-height: 157%;
  }

  // .review__date-text

  &__date-text {
    font-weight: 700;
  }

  // .review__date-value

  &__date-value {
  }

  // .review__response
}
