@use '_mixin' as m;

.account-reviews {
  width: 100%;
  // .account-reviews__statistics

  &__statistics {
    margin-bottom: m.toRem(20);
  }

  // .account-reviews__body

  &__body {
  }

  // .account-reviews__filters

  &__filters {
    margin-bottom: m.toRem(16);
    display: flex;
    align-items: center;
  }

  // .account-reviews__filters-items

  &__filters-items {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    gap: m.toRem(10);

    @media (max-width: m.toEm(768)) {
      flex-wrap: wrap;
    }
  }

  // .account-reviews__filters-search

  &__filters-search {
    flex: 1 1 auto;

    @media (max-width: m.toEm(768)) {
      width: 100%;
    }

    .input__control {
      font-size: m.toRem(14);
      height: m.toRem(40);
    }
  }

  // .account-reviews__filters-select

  &__filters-input {
    flex: 0 0 m.toRem(140);
    @media (max-width: m.toEm(768)) {
    }
    // .input__control

    .input__control {
      height: m.toRem(40);
      &::placeholder {
        color: var(--main-color);
      }
    }
  }

  // .account-reviews__items

  &__items {
    &:not(:last-child) {margin-bottom: m.toRem(20);}
    display: flex;
    flex-direction: column;
    gap: m.toRem(20);
  }
}
