@use '_mixin' as m;

.account-statistics {
  display: flex;
  gap: m.toRem(30);

  @media (max-width: m.toEm(1280)) {
    gap: m.toRem(20);
  }
  @media (max-width: m.toEm(768)){
       gap: m.toRem(10);
  }

  // .account-statistics__item

  &__item {
    padding: m.toRem(20) m.toRem(20);
    display: grid;
    grid-template-columns: m.toRem(50) 1fr;
    gap: m.toRem(20) m.toRem(14);
    box-shadow: 0 1px 3px 0 rgba(166, 175, 195, 0.4);
    background: var(--white);
    border-radius: 10px;
    @media (max-width: m.toEm(768)) {
      grid-template-columns: m.toRem(40) 1fr;
      padding: m.toRem(15) m.toRem(10) ;
      gap: m.toRem(10) m.toRem(10);
    }
  }

  // .account-statistics__img

  &__img {
    width: m.toRem(50);
    height: m.toRem(50);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;

    &--reviews {
      background: rgba(19, 194, 150, 0.08);
      .account-statistics__icon {
        width: m.toRem(26);
        height: m.toRem(26);
        @media (max-width: m.toEm(768)) {
          width: m.toRem(20);
          height: m.toRem(20);
        }
      }
    }
    &--reads {
      background: rgba(55, 87, 249, 0.08);
      .account-statistics__icon {
        width: m.toRem(24);
        height: m.toRem(24);
        @media (max-width: m.toEm(768)) {
          width: m.toRem(20);
          height: m.toRem(20);
        }
      }
    }
    @media (max-width: m.toEm(768)) {
      width: m.toRem(40);
      height: m.toRem(40);
    }
  }

  // .account-statistics__icon

  &__icon {
  }

  // .account-statistics__info

  &__info {
  }

  // .account-statistics__title

  &__title {
    font-weight: 500;
    line-height: 150%;
  }

  // .account-statistics__text

  &__text {
    font-size: m.toRem(14);
    line-height: 157%;
    color: var(--text);
  }

  // .account-statistics__value

  &__value {
    grid-column: 1 / 3;
    font-weight: 700;
    font-size: m.toRem(24);
    @media (max-width: m.toEm(768)) {
      font-size: m.toRem(20);
    }
  }
}
