
@use '_mixin' as m;
.checkbox {
  // .checkbox__input

  &__input {
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }

}

.checkbox.error .checkbox__label::before {
  box-shadow: 0 0 0 1px red;
}

.checkbox__input:checked+.checkbox__label::after {
  transform: scale(1);
}

.checkbox__input:checked+.checkbox__label::before {
  background-color: var(--green);
}

.checkbox__input:focus-visible+.checkbox__label:before {}

.checkbox__label {
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-items: start;
  position: relative;
  cursor: pointer;
  
}

.checkbox__label::before {
  content: "";
  align-self: flex-start;
  flex-shrink: 0;
  height: m.toRem(20);
  width: m.toRem(20);
  border-radius: 4px;
  margin: 0 m.toRem(12) 0 0;
  outline: 2px solid var(--grey);
  transition: background 0.3s ease 0s;
}

.checkbox__label::after {
  content: "";
  width: m.toRem(10);
  height: m.toRem(8);
  position: absolute;
  top: m.toRem(6);
  left: m.toRem(5);
  background: url('../img/svg/check.svg') center / contain no-repeat;

  transform: scale(0);
  transition: transform 0.3s ease;
}



.checkbox__label-text {
  font-size: m.toRem(13);
}

.checkbox__label-link {
  font-weight: 600;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  color: var(--red);

  &:hover {
    text-decoration: none;
  }
}