@use '_mixin' as m;

.rating-company {

  display: flex;
  flex-direction: column;
  max-width: m.toRem(370);


  // .rating-company__body

  &__body {
    margin-bottom: m.toRem(11);
    padding: m.toRem(15) m.toRem(15) m.toRem(27);
    border: 1px solid var(--grey-border);
    border-radius: 8px;
    display: flex;
    align-items: end;
    gap: m.toRem(15);
    @media (max-width: m.toEm(768)) {
      padding: m.toRem(15) m.toRem(7) m.toRem(20);
    }
    @media (max-width: m.toEm(359)) {
      padding: m.toRem(15) m.toRem(0) m.toRem(20);
      border: 0;
    }
  }

  // .rating-company__gauge

  &__gauge {
    flex-shrink: 0;
    margin-left: m.toRem(0);
    margin-top: m.toRem(-25);
  }

  // .rating-company__legend

  &__legend {
    display: flex;
    flex-direction: column;
    gap: m.toRem(10);
    @media (max-width: m.toEm(768)) {
      gap: m.toRem(8);
    }
  }

  // .rating-company__legend-text

  &__legend-text {
    font-size: m.toRem(12);
    position: relative;
    display: flex;
    align-items: center;
    gap: m.toRem(9);
    &::before {
      content: '';
      flex-shrink: 0;
      width: m.toRem(6);
      height: m.toRem(6);
      border-radius: 50%;
    }

    &--5 {
      &::before {
        background-color: var(--green);
      }
    }
    &--4 {
      &::before {
        background-color: var(--teal);
      }
    }
    &--3 {
      &::before {
        background-color: var(--orange);
      }
    }
    &--2 {
      &::before {
        background-color: var(--light-red);
      }
    }
    &--1 {
      &::before {
        background-color: var(--red);
      }
    }
  }

  // .rating-company__info

  &__info {
    display: flex;
    flex-direction: column;
    align-items: start;
  }

  // .rating-company__notification

  &__notification {
    margin-bottom: m.toRem(12);
    padding: m.toRem(12) m.toRem(17);
    display: flex;
    align-items: center;
    gap: m.toRem(8);
    border: 1px solid var(--green);
    background-color: #d8fff5;
    border-radius: 8px;
    font-size: m.toRem(14);
    line-height: 120%;
    color: var(--green);
    position: relative;
    &::before {
      content: '';
      flex-shrink: 0;
      width: m.toRem(30);
      height: m.toRem(30);
      background: url('../img/svg/secure.svg') 0 0 / contain no-repeat;
    }

    @media (max-width: m.toEm(768)) {
      padding: m.toRem(10) m.toRem(15);
      font-size: m.toRem(13);
    }
  }

  &__text {
    margin: 0 auto;
    max-width: m.toRem(290);
    text-align: center;
    font-size: m.toRem(10);
    line-height: 130%;
  }
}

.gauge {
  width: m.toRem(217);
  height: m.toRem(130);
  position: relative;

  // .gauge__background

  &__background {
    position: absolute;
    width: 114%;
    height: 100%;
    left: -12%;
    bottom: 0;
    display: block;
  }

  // .gauge__arrow-wrapper

  &__arrow-wrapper {
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 0px;
    height: m.toRem(82);
    transform: translate(50%, 0px);
    transform: rotate(var(--angle));
    transform-origin: center bottom;
    pointer-events: none;
  }

  // .gauge__arrow

  &__arrow {
    position: absolute;
    top: 0;
    transform: translate(-50%, 0px);
    width: m.toRem(17);
    height: m.toRem(17);
    fill: var(--color);
  }

  // .gauge__arrow-icon

  &__arrow-icon {
    width: m.toRem(17);
    height: m.toRem(17);
    fill: var(--color);
  }

  // .gauge__scores

  &__scores {
    position: absolute;
    bottom: m.toRem(-7);
    left: 50%;
    transform: translate(-50%, 0px);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: m.toRem(4);
  }

  // .gauge__value

  &__value {
    font-weight: 700;
    font-size: m.toRem(48);
    line-height: 67%;
  }

  // .gauge__label

  &__label {
  }
}
