@use '_mixin' as m;

.settings {
  width: 100%;
  max-width: m.toRem(570);
  padding-bottom: m.toRem(2);

  // .settings__form

  &__form {
    display: flex;
    flex-direction: column;
    gap: m.toRem(30);
  }

  // .settings__block

  &__block {
    padding: m.toRem(20) m.toRem(20);

    box-shadow: 0 1px 3px 0 rgba(166, 175, 195, 0.4);
    background: var(--white);
    border-radius: 10px;
    @media (max-width: m.toEm(768)) {
      padding: m.toRem(20) m.toRem(15);
    }
  }

  // .settings__title

  &__title {
    margin-bottom: m.toRem(10);
    font-weight: 600;
    font-size: m.toRem(20);
    line-height: 150%;
  }

  // .settings__items

  &__items {
    display: flex;
    flex-direction: column;
    gap: m.toRem(10);

    @media (max-width: m.toEm(1280)) {
      gap: m.toRem(12);
    }
    &:not(:last-child) {
      margin-bottom: m.toRem(10);
    }
    .input__control {
      color: #9ca3af;
    }
  }

  // .settings__button

  &__button {
    padding: m.toRem(4) m.toRem(16);
    height: m.toRem(40);
    min-width: m.toRem(160);
  }

  // .settings__notif

  &__notif {
    display: flex;
    gap: m.toRem(15);
    align-items: center;
    justify-content: space-between;
  }

  &__notif-button {
  }

  // .settings__notif-text

  &__notif-text {
    font-size: m.toRem(14);
    line-height: 171%;
  }

  // .settings__action

  &__action {
  }

  // .settings__action-text

  &__action-text {
    &:not(:last-child) {
      margin-bottom: m.toRem(16);
    }
    font-size: m.toRem(14);
    line-height: 130%;
    color: var(--text);
  }

  // .settings__action-btn

  &__action-btn {
  }
}

.file {
  &__upload {
    margin-bottom: m.toRem(10);
    display: flex;
    align-items: center;
    gap: m.toRem(16);
  }

  &__preview {
    width: m.toRem(100);
    height: m.toRem(100);
    overflow: hidden;
    border-radius: 50%;
    img {
      width: 100%;
      height: 100%;
      object-position: center;
      object-fit: cover;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    align-items: start;
    font-size: m.toRem(12);
    line-height: 130%;
  }

  &__button {
  }

  // .file__item

  &__item {
    position: relative;
  }

  // .file__input

  &__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    font-size: 0;
    cursor: pointer;
  }
}
