@use '_mixin' as m;
.write-review {
  // .write-review__header

  &__header {
    margin-bottom: m.toRem(30);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: m.toRem(11);
  }

  // .write-review__logo

  &__logo {
    flex-shrink: 0;
    width: m.toRem(60);
    height: m.toRem(60);
    overflow: hidden;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
  }

  &__company {
    display: flex;
    flex-direction: column;
    gap: m.toRem(3);
    align-items: start;
  }

  &__name {
    font-family: var(--second-family);
    font-weight: 600;
    font-size: m.toRem(24);
  }

  &__address {
    font-size: m.toRem(14);
    text-decoration: underline;
    text-decoration-skip-ink: none;
    @media (any-hover: hover) {
      &:hover {
        text-decoration: none;
      }
    }
  }

  // .write-review__form

  &__form {
  }

  // .write-review__items

  &__items {
    margin-bottom: m.toRem(24);
  }

  // .write-review__rating-group

  &__rating-group {
    margin-bottom: m.toRem(19);
    align-self: start;
  }

  // .write-review__info

  &__info {
    font-weight: 500;
    font-size: m.toRem(12);
    line-height: 135%;
  }
}
